var roman_map = {
  M: 1000,
  CM: 900,
  D: 500,
  CD: 400,
  C: 100,
  XC: 90,
  L: 50,
  XL: 40,
  X: 10,
  IX: 9,
  V: 5,
  IV: 4,
  I: 1
};
var allChars = Object.keys(roman_map);
var allNumerals = Object.values(roman_map);

var romanize = function romanize(decimal) {
  if (decimal <= 0 || typeof decimal === 'undefined' || typeof decimal === 'string') {
    throw new Error('requires an unsigned integer');
  }

  var roman = '';

  for (var i = 0; i < allChars.length; i++) {
    while (decimal >= allNumerals[i]) {
      decimal -= allNumerals[i];
      roman += allChars[i];
    }
  }

  return roman;
};

var deromanize = function deromanize(romanStr) {
  var romanString = romanStr.toUpperCase();
  var arabic = 0;
  var iteration = romanString.length;

  while (iteration--) {
    var cumulative = roman_map[romanString[iteration]];

    if (cumulative < roman_map[romanString[iteration + 1]]) {
      arabic -= cumulative;
    } else {
      arabic += cumulative;
    }
  }

  return arabic;
};

module.exports = {
  deromanize: deromanize,
  romanize: romanize,
  allChars: allChars,
  allNumerals: allNumerals
};